
.docsign {
  // font-size: 16px;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  &:not(.modal) {
    background-color: #fff;
  }

  b,
  th {
    font-weight: 600;
  }
  .text-site {
    color: #2c62d6;
  }

  @media (min-width: 1400px) {
    .container,
    .container-lg,
    .container-md,
    .container-sm,
    .container-xl {
      max-width: 1360px;
    }
  }

  .btn_main {
    background-color: #0d7aca;
    color: #fff;
    border-color: #0d7aca;
    padding: 8px 35px;
    border-radius: 4px;
    transition: all 0.3s ease-in-out;
  }

  .btn_main:hover {
    background-color: #2c62d6;
    color: #fff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.25);
  }

  .btn_site {
    background-image: linear-gradient(to left, #416bb3 0%, #479fd4 51%, #416bb3 100%) !important;
    transition: all 0.3s ease-in-out;
    background-size: 200% auto;
    color: #fff;
    border: 0;
    padding: 8px 35px;
    border-radius: 6px;
  }

  .btn_site:hover {
    color: #fff;
    background-position: right center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.25);
  }

  .btn_outline_site {
    transition: all 0.3s ease-in-out;
    color: #2c62d6;
    border-color: #2c62d6;
    padding: 7px 34px;
    border-radius: 6px;
  }

  .btn_outline_site:hover {
    color: #fff;
    background-color: #2c62d6;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.25);
  }

  .btn_outline_main {
    transition: all 0.3s ease-in-out;
    color: #6c757d;
    border-color: #6c757d;
    padding: 7px 34px;
    border-radius: 6px;
  }

  .btn_outline_main:hover {
    color: #0d7aca;
    border-color: #0d7aca;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.25);
  }

  @media (max-width: 767px) {
    .btn_site {
      padding: 8px 20px;
    }

    .btn_outline_site {
      padding: 7px 19px;
    }
  }

  /* Docsign Page */
  .docsign_header {
    color: #fff;
    line-height: 1.4;
  }

  .docsign_header .top_border {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 50px;
    background: linear-gradient(to right, #416bb3, #479fd4);
    @media screen and (max-width: 991px) {
      height: 49px;
    }
  }

  .docsign_header .top_border:before {
    content: ' ';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    border-bottom: 50px solid #fff;
    border-right: 70px solid transparent;
  }

  .docsign_header .top_border:after {
    content: ' ';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    border-bottom: 50px solid #fff;
    border-left: 70px solid transparent;
  }

  .logo_wrap {
    background: #fff;
    border-radius: 50%;
    padding: 3px;
    margin-top: 3px;
    position: absolute;
    left: calc(50% - 41px);
    top: 0;
    width: 82px;
    height: 82px;
    text-align: center;
  }

  .img_alt {

    cursor: pointer;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #eee;
    font-size: 35px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    border: 3px solid #fff;
    color: #bbb;
  }

  .img_alt span {
    margin: auto;
    text-transform: uppercase;
  }

  .logo_wrap img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    border-radius: 50%;
    position: relative;
    z-index: 1;
    background: #fff;
  }

  .header_text .col {
    height: 50px;
  }

  .docsign_header .company_name {
    font-weight: 600;
  }

  .docsign_main {
    margin-left: 70px;
    margin-right: 70px;
  }

  @media (max-width: 1200px) {
    .docsign_main {
      margin-left: 24px;
      margin-right: 24px;
    }
  }

  .docsign_main .main_wrap {
    border: 1px solid #d3d3d3;
    border-radius: 15px;
  }

  .docsign_main .contract_name {
    border-bottom: 1px solid #d3d3d3;
  }

  .docsign_main .contract_name h1 {
    font-size: 18px;
    text-transform: uppercase;
    font-weight: 600;
    text-align: center;
    margin: 16px 0;
    line-height: 1.4;
    color: #505050;
    transform: scale(0.95, 1);
  }

  @media (max-width: 767px) {
    .docsign_main .contract_name h1 {
      font-size: 17px;
    }
  }

  .pages {
    min-height: 383px;
    max-height: calc(100vh - 226px);
    overflow-y: auto;
    position: relative;
  }

  .pages img {
    border: 1px solid #e1e1e1;
  }

  .pages img.page_active {
    border: 1px solid #2c62d6;
  }

  .preview_nav {
    height: 28px;
    overflow: hidden;
  }

  .preview_body {
    min-height: 356px;
    max-height: calc(100vh - 254px);
    background: #ededf0;
    border: 1px solid #e1e1e1;
    position: relative;
    overflow-y: auto;
  }

  @media (min-height: 611px) {
    .preview_body {
      min-height: calc(100vh - 254px);
    }
  }

  .doc_preview .actions a {
    padding: 5px;
  }

  .doc_preview .actions a:hover {
    background: #ddd;
  }

  .doc_preview .pages_nums input {
    width: 50px;
    height: 25px;
    font-size: 16px;
  }

  .doc_preview .nav_btn a {
    text-decoration: none;
    color: #212529;
    padding: 6px;
    font-size: 18px;
    line-height: 18px;
  }

  .doc_preview .nav_btn a:hover {
    color: #2c62d6;
  }

  .info_tabs .nav-pills .nav-link {
    border-radius: 0;
    padding: 2px;
    border-bottom: 1px solid #d3d3d3;
    border-right: 1px solid #d3d3d3;
    color: #333;
  }

  .info_tabs .nav-pills .nav-item:first-child .nav-link {
    border-left: 1px solid #d3d3d3;
  }

  .info_tabs .nav-pills .nav-item:first-child .nav-link.active {
    border-left: 1px solid #0d7aca;
  }

  .info_tabs .nav-pills .nav-item:last-child .nav-link {
    border-right: 0;
  }

  .info_tabs .nav-pills .nav-link:hover {
    background-color: #eee;
    color: #0056b3;
  }

  .info_tabs .nav-pills .nav-link.active {
    background-color: #0d7aca;
    border-color: #0d7aca;
    color: #fff;
  }

  .info_tabs .nav-link svg {
    margin-top: -4px;
  }

  .info_tabs .tab_title {
    font-weight: 600;
    color: #2299d5;
  }

  .footer_text {
    height: 36px;
    background: linear-gradient(to right, #416bb3, #479fd4);
    color: #fff;
  }

  .sign_progress {
    color: #fff;
    background: linear-gradient(to right, #18c018, #95e195);
    font-weight: 600;
    font-size: 14px;
    position: relative;
  }
  .bg_sign_progress {
    top: 0;
    position: absolute;
  }

  .signer_item {
    cursor: pointer;
    border-bottom: 1px solid #d3d3d3;
  }

  .signer_item:last-child {
    border-bottom: 0;
  }

  .person_img_wrap {
    width: 36px;
    height: 36px;
    background: #fff;
    position: relative;
    border-radius: 50%;
  }

  .person_img_wrap .img_alt {
    border: 0;
    font-size: 18px;
  }

  .signer_item .person_img,
  .comment_item .person_img,
  .noti_item .person_img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1;
    position: relative;
  }
  .person_img {
    max-width: auto;
    max-height: auto;
    object-fit: cover;
    border: none;
    padding: 0;
  }
  .comment_reply.new,
  .comment_item.new {
    background: #d7eeff;
    border-radius: 6px;
  }

  .comment_reply.new .person_img_wrap:after,
  .comment_item.new .person_img_wrap:after {
    content: ' ';
    position: absolute;
    top: -2px;
    right: -2px;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: #3a9bff;
    z-index: 2;
  }

  .signer_item .person_text {
    overflow: hidden;
  }

  .signer_item .person_name,
  .comment_item .person_name {
    cursor: pointer;
    color: #0d7aca;
    font-weight: 600;
    margin-bottom: 3px;
  }

  .signer_item .done_icon {
    margin-top: -2px;
  }

  .signer_item .person_meta {
    font-size: 14px;
    color: #505050;
    margin-bottom: 4px;
    display: flex;
    align-items: center;
    span {
      padding-left: 5px;
    }
  }

  .signer_item .person_meta:last-child {
    margin-bottom: 2px;
  }

  .signer_item .person_email {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .comment_inpreview {
    position: absolute;
    cursor: pointer;
    // top: 32%;
    // left: 57%;
  }

  .comment_inpreview span {
    color: #fff;
    position: absolute;
    left: 0;
    right: 0;
    text-align: center;
    font-size: 14px;
    font-weight: 600;
    line-height: 26px;
  }

  .comment_text {
    font-size: 15px;
    max-width: calc(100% - 44px);
  }

  .comment_meta {
    cursor: default;
    font-size: 13px;
    color: #666;
  }

  .comment_meta a {
    color: #0d7aca;
  }

  .comment_meta a:hover {
    color: #2c62d6;
  }

  #docsignTabContent {
    width: 330px;
    min-height: 355px;
    max-height: calc(100vh - 255px);
    overflow-y: auto;
    overflow-x: hidden;
  }

  .comment_view {
    position: absolute;
    cursor: pointer;
    top: 20px;
    right: -4px;
    z-index: 2;
  }

  .comment_view span {
    color: #fff;
    position: absolute;
    left: 0;
    right: 0;
    text-align: center;
    font-size: 12px;
    font-weight: 600;
    line-height: 23px;
  }

  .doc_preview .actions a svg,
  .doc_preview .nav_btn a svg {
    margin-top: -3px;
  }

  .comment_text textarea {
    font-size: 15px;
    padding: 6px 11px 14px 11px;
    min-height: 62px;
    height: 62px;
  }

  #commentModal .modal-body {
    padding-top: 8px;
  }

  .comment_btn {
    margin-top: 7px;
  }

  .comment_btn .btn {
    font-size: 15px;
    padding: 1px 9px;
  }

