}

.comment_btn .btn:first-child{
	color: #0d7aca;
	font-weight: 600;
}

.comment_btn .btn:first-child:hover{
	color: #2c62d6;
}

.comment_btn .btn:last-child{
	color: #666;
}

.comment_btn .btn:last-child:hover{
	color: #333;
