  .back_btn {
    position: absolute;
    top: 8px;
    left: 18px;
    z-index: 3;
  }

  .btn_back {
    padding: 0;
    width: 34px;
    height: 34px;
    border: 1px solid #0d7aca;
    color: #0d7aca;
    background: rgba(255, 255, 255, 0.9);
    border-radius: 18px;
    transition: all 0.3s ease-in-out;
  }

  .btn_back:hover {
    background: #fff;
    color: #2c62d6;
    border-color: #2c62d6;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.25);
  }

  .modal_site .modal-header {
    position: relative;
    padding: 8px 16px;
    border-bottom: 2px solid #3a9bff;
  }

  .modal_site .modal-header h5 {
    color: #0d7aca;
    font-weight: 400;
    font-size: 19px;
  }

  .new_contract_nav button {
    width: 140px;
  }

 

  #tagsModal .tag_color {
    width: 21px;
    height: 21px;
  }

  .checktag_item {
    border-bottom: 1px solid #e5e5e5;
    padding: 3px 0;
    height: 45px;
    transition: ease 0.25s;
  }

  .checktag_item:last-child {
    border-bottom: 0;
  }

  .checktag_item:hover {
    background: #e0f7ff;
  }

  .checktag_item:hover .tagedit_btn {
    opacity: 1;
  }

  .checktag_item .tagedit_btn {
    opacity: 0;
    width: 38px;
    height: 38px;
    border-radius: 20px;
    text-align: center;
    padding: 0;
    color: #777;
    font-size: 21px;
    display: inline-block;
    transition: ease 0.25s;
    line-height: 33px;
  }

  .checktag_item .tagedit_btn:hover {
    text-decoration: none;
    background: #dfdfdf;
  }

  .quick_addtag {
    cursor: pointer;
    color: #666;
  }

  .quick_addtag:hover {
    color: #212529;
  }

  .quick_addtag svg {
    font-size: 22px;
    color: #666;
  }

  .newtag_name,
  .tag_search {
    font-size: 16px;
  }

  .flip_hor {
    transform: scale(-1, 1);
  }

  .custom-control-label::before,
  .custom-control-label::after {
    width: 1.25rem;
    height: 1.25rem;
    top: 2px;
    left: -1.65rem;
  }

  .color_palette {
    position: absolute;
    width: 196px;
    z-index: 1;
    background: #fff;
    border-radius: 6px;
    border: 1px solid #ccc;
    top: -90px;
    left: -44px;
    padding: 10px 0 5px 10px;
    transition: ease 0.15s;
    visibility: hidden;
    opacity: 0;
  }

  .color_select:hover .color_palette {
    visibility: visible;
    opacity: 1;
  }

  .color_palette span {
    width: 25px;
    height: 25px;
    display: inline-block;
    border-radius: 6px;
    margin: 3px;
    cursor: pointer;
  }

  .color_palette span:hover {
    box-shadow: 0 2px 6px #666;
  }

  .float_noti {
    width: 400px;
    position: fixed;
    bottom: 15px;
    left: 15px;
    z-index: 2;
    max-width: calc(100vw - 30px);
    display: flex;
    flex-direction: column-reverse;
  }

  a.noti_item {
    padding: 10px 0;
    border-radius: 8px;
    text-decoration: none;
    display: block;
    border: 1px solid #d9d9d9;
    background: #fff;
    margin-top: 15px;
    position: relative;
  }

  a.noti_item:hover {
    background: #e9e9e9;
  }

  /*.noti_item .person_img{
	max-width: 55px;
	max-height: 55px;
	border: 0;
	padding: 0;
}*/

  .pimg_alt {
    width: 55px;
    height: 55px;
    border-radius: 50%;
    display: block;
    text-align: center;
    font-size: 25px;
    line-height: 55px;
    text-transform: uppercase;
    color: #555;
    background: #f2c94c;
  }

  .noti_sicon {
    width: 28px;
    height: 28px;
    background: #b5b5b5;
    border-radius: 50%;
    position: absolute;
    top: 32px;
    right: -5px;
    color: #fff;
    text-align: center;
    line-height: 25px;
    z-index: 2;
  }

  .unread .noti_sicon {
    background: #3a9bff;
  }

  .noti_sicon svg {
    margin-top: -1px;
  }

  .noti_text,
  .noti_name,
  .noti_content {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .noti_name,
  .unread .noti_meta {
    font-weight: 600;
  }

  .noti_name,
  .noti_meta,
  .noti_content {
    color: #888;
  }

  .unread .noti_meta .time {
    color: #0d7aca;
  }

  #expiry .unread .noti_meta .time {
    color: #dc3545;
  }

  .unread .noti_name,
  .unread .noti_meta {
    color: #353f4d;
  }

  .unread .noti_content {
    color: #555;
  }

  .fn_close {
    opacity: 0;
    position: absolute;
    right: -10px;
    top: -10px;
    font-size: 21px;
    width: 22px;
    height: 22px;
    background: #ddd;
    text-align: center;
    font-weight: 300;
    color: #333;
    line-height: 0.8;
    border-radius: 50%;
    transition: ease 0.25s;
    border: 1px solid #d5d5d5;
  }

  .fn_close:hover {
    background: #c1c1c1;
    border-color: #c1c1c1;
  }

  a.noti_item:hover > .fn_close {
    opacity: 1;
  }

  .noti_item .person_img_wrap {
    width: 55px;
    height: 55px;
  }

  @media (hover: none) {
    .fn_close {
      opacity: 1;
    }
  }

  .signature_inpreview {
    position: absolute;
    cursor: pointer;
    background: rgba(255, 255, 255, 1);
    border: 1px dashed #ffc232;
    border-radius: 5px;
    text-align: center;
    padding: 5px;
    color: #666;
    max-width: 20%;
    height: 9%;
    display: flex;
    align-items: center;
    overflow: hidden;
    min-height: 98px;
  }

  .signature_inpreview.signed {
    border: 1px dashed #1d95ff;
    background: url('../images/check.svg') no-repeat center;
    background-color: rgba(203, 235, 250, 1);
    background-size: 90%;
  }

  .signature_inpreview.me {
    background: rgba(255, 255, 255, 1);
    border: 1px dashed #1d95ff;
  }

  .signature_inpreview.me.signed {
    border: 1px dashed #ffc232;
    background: url('../images/check.svg') no-repeat center;
    background-color: rgba(255, 236, 132, 1);
    background-size: 90%;
  }

  .signature_inpreview .signer_icon {
    font-size: 24px;
    color: #999;
    margin-bottom: 3px;
  }

  .signature_inpreview.signed .signer_icon {
    color: #00a334;
  }

  .signature_inpreview .signer_name {
    font-size: 1.1vw;
    font-weight: 600;
    line-height: 1.4;
    color: #000;
  }

  @media (max-width: 991px) {
    .signature_inpreview .signer_name {
      font-size: 1.9vw;
    }
  }
  @media (max-width: 576px) {
    .signature_inpreview {
      min-width: 62px !important;
      max-width: 62px !important;
      min-height: 36px !important;
    }
    .signature_inpreview {
      padding: 5px !important;
    }
  }

  .page_item {
    position: relative;
  }

  .si_area {
    position: absolute;
    top: 0;
    left: 1px;
    max-height: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }

  .signature_icon {
    cursor: pointer;
    width: 25px;
    height: 22px;
    text-align: center;
    background: rgba(255, 255, 255, 1);
    border: 1px dashed #ffc232;
    border-radius: 4px;
    margin-top: 1px;
    color: #888;
  }
  .signature_icon.signed {
    background: rgba(255, 236, 132, 1);
  }
  .signature_icon.me {
    background: rgba(255, 255, 255, 1);
    border: 1px dashed #1d95ff;
  }
  .signature_icon.me.signed {
    background: rgba(203, 235, 250, 1);
  }
  .signature_icon svg {
    font-size: 14px;
    margin-top: -10px;
  }
  .signature_icon.signed svg {
    font-size: 24px;
  }
  .signature_icon.signed {
    color: #008e01;
  }

  .signature_inpreview .signed_check {
    color: #43cd80;
    opacity: 0.47;
    position: absolute;
    top: -10%;
    left: 30%;
  }
  .signature_inpreview {
    position: absolute;
    width: auto;
    min-width: 165px;
    height: auto;
    min-height: 97px;
    font-size: 16px;
    z-index: 3;
    overflow: unset;
  }
  @media (max-width: 1024px) {
    .signature_inpreview {
      min-width: 109px;
      min-height: 64px;
      .signer_icon {
        font-size: 16px;
      }
    }
  }
  @media (max-width: 768px) {
    .signature_inpreview {
      min-width: 140px;
      min-height: 82px;
      .signer_icon {
        font-size: 20px;
      }
    }
  }

  @media (max-width: 576px) {
    .signature_inpreview {
      min-width: 61px;
      min-height: 36px;
      .signer_icon {
        font-size: 8px;
      }
    }
  }
  
  .react-pdf__Document {
    position: relative;
    .pdf-page-container:not(:last-child) {
      .spliter-page-preview {
          margin-bottom: 1rem;
      }
      .spliter-page {
        margin-bottom: 10px;
      }
   }
  }
  /* End Docsign Page */
}
.comment_item.new :hover {
  background: #fff;
}
.new_contract_nav button {
  min-width: 140px;
}

.comment-icon {
  cursor: url('../images/comment.svg'), auto;
