@media screen and (min-width: 1200px) {
  .docsign.has-signed {
    height: 100vh;
    display: flex;
    flex-flow: column;
    .docsign_main  {
      flex-grow: 1;
      .main_wrap {
        display: flex;
        flex-flow: column;
        height: 100%;
        max-height: calc(100% - 20px);
        .container-fluid {
          flex-grow: 1;
        }
      }

      .preview_body {
        max-height: calc(100vh - 217px);
        min-height: calc(100vh - 217px);
      }
    }
  
    #docsignTabContent {
        min-height: 390px;
        max-height: calc(100vh - 220px);
    }
    .pages {
      min-height: 418px;
      max-height: calc(100vh - 190px);
    }
  }
}

@media screen and (max-width: 1200px) {
  .docsign.has-signed {
    .docsign_main  {
      margin-bottom: 24px;
    }
  }
}

@media screen and (max-width: 576px) and (orientation: portrait) {
  .docsign.has-signed {
    .preview_body {
        min-height: calc(100vh - 220px);
    }
  }
}
.comment_item {
  transition: 1s;
}