.create-contract {
  font-size: 16px;
  /* New Contract Steps*/
  padding-left: 0 !important;
  padding-right: 0 !important;
  b {
    font-weight: 600;
  }
  .text-site {
    color: #2c62d6;
  }

  .modal_full {
    max-width: 1200px;
  }

  .modal_full .modal-content {
    min-height: 100vh;
  }
  .react-pdf__Document {
    position: relative;
    .pdf-page-container:not(:last-child) {
      .spliter-page-preview {
        margin-bottom: 1rem;
      }
      .spliter-page {
        margin-bottom: 10px;
      }
    }
  }
  .new_contract_header {
    box-shadow: 0 4px 3px rgba(0, 0, 0, 0.15) !important;
    position: relative;
    height: 146px;
    overflow: hidden;
  }

  @media (max-width: 767px) {
    .new_contract_header {
      height: 121px;
      overflow: hidden;
    }
  }

  .new_contract_header .close_btn {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 37px;
    line-height: 1;
    z-index: 1;
  }

  .new_contract_header .close_btn a {
    color: #777;
    text-decoration: none;
    padding: 0 11px;
    transition: ease 0.25s;
    font-weight: 300;
  }

  .new_contract_header .close_btn a:hover {
    color: #2c62d6;
  }

  .new_contract_header img {
    // max-width: 137px;
    width: 137px;
  }

  .new_contract_header h1 {
    font-size: 28px;
    color: #2c62d6;
    font-weight: 600;
  }

  .new_contract_header .small_steps {
    display: none;
  }

  .new_contract_header .small_steps h3 {
    font-size: 16px;
    text-align: center;
    font-weight: 400;
    margin: 12px 0;
    color: #2c62d6;
  }

  .new_contract_header .small_steps .progress {
    height: 12px;
    border-radius: 6px;
  }

  .new_contract_header .small_steps .progress-bar {
    border-radius: 6px;
  }

  @media (max-width: 767px) {
    .new_contract_header .large_steps {
      display: none;
    }

    .new_contract_header .small_steps {
      display: block;
    }
  }

  @media (max-width: 767px) {
    .new_contract_body h4 {
      font-size: 20px;
    }
  }

  .new_contract_body .top_border {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 9px;
    background: linear-gradient(to right, #48b0e0, #2c62d6);
    z-index: 999;
  }

  .new_contract_body .ncbody_wrap {
    border: 1px solid #2c62d6;
    border-top: 0;
    position: relative;
    display: flex;
    justify-content: center;
    overflow: auto;
    flex-direction: column;
    overflow-x: hidden;
  }
  .ncbody_wrap_step1 {
    cursor: pointer;
  }
  .new_contract_body .ncbody_wrap.has_ncnav {
    min-height: calc(100vh - 356px);
  }
  .new_contract_body .ncbody_wrap.has_ncnav.step3-wrap {
    min-height: auto;
  }
  // .new_contract_body .partner_wrap  {
  //    // max-height: calc(100vh - 329px);
  //    overflow-y: auto;
  // }
  .new_contract_body .partner_wrap .ps__rail-y {
    z-index: 9999;
  }
  @media (max-width: 767px) {
    .new_contract_body .ncbody_wrap {
      min-height: calc(100vh - 259px);
    }
    .new_contract_body .ncbody_wrap.step3-wrap {
      min-height: auto;
    }
    .new_contract_body .ncbody_wrap.step3-wrap.has_ncnav {
      min-height: auto;
    }
    .new_contract_body .ncbody_wrap.has_ncnav {
      min-height: calc(100vh - 331px);
    }
