  .new_contract_body .top_border:before {
    content: ' ';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    border-bottom: 10px solid #fff;
    border-right: 20px solid transparent;
  }

  .new_contract_body .top_border:after {
    content: ' ';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    border-bottom: 10px solid #fff;
    border-left: 20px solid transparent;
  }

  .new_contract_body .lefttop_corner {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 10px;
    height: 60px;
    background: linear-gradient(to top, #48b0e0 15%, #2c62d6);
    z-index: 999;
  }

  .new_contract_body .lefttop_corner:before {
    content: ' ';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    border-top: 20px solid #fff;
    border-left: 10px solid transparent;
  }

  .new_contract_body .leftbottom_corner {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 60px;
    height: 10px;
    background: linear-gradient(to right, #48b0e0 15%, #2c62d6);
    z-index: 999;
  }

  .new_contract_body .leftbottom_corner:after {
    content: ' ';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    border-top: 10px solid #fff;
    border-left: 20px solid transparent;
  }

  .new_contract_body .righttop_corner {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 10px;
    height: 60px;
    background: linear-gradient(to top, #48b0e0 15%, #2c62d6);
    z-index: 999;
  }

  .new_contract_body .righttop_corner:before {
    content: ' ';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    border-top: 20px solid #fff;
    border-right: 10px solid transparent;
  }

  .new_contract_body .rightbottom_corner {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 60px;
    height: 10px;
    background: linear-gradient(to left, #48b0e0 15%, #2c62d6);
    z-index: 999;
  }

  .new_contract_body .rightbottom_corner:before {
    content: ' ';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    border-top: 10px solid #fff;
    border-right: 20px solid transparent;
  }

  .new_contract_footer {
    color: #fff;
    background: linear-gradient(to right, #2c62d6, #48b0e0);
    border-radius: 0 0 4px 4px;
    height: 40px;
    overflow: hidden;
  }

  @media (max-width: 767px) {
    .new_contract_footer {
      height: 88px;
      overflow: hidden;
    }
  }

  .new_contract_footer a {
    color: #fff;
  }

  /* For Step Nav */
  .sw-theme-default {
    border: 1px solid #eee;
  }
  .sw-theme-default > .tab-content > .tab-pane {
    padding: 10px;
  }
  .sw-theme-default .toolbar > .btn {
    color: #fff;
    background-color: #17a2b8;
    border: 1px solid #17a2b8;
    padding: 0.375rem 0.75rem;
    border-radius: 0.25rem;
    font-weight: 400;
  }
  .sw-theme-default > .nav {
    box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.1) !important;
  }
  .sw-theme-default > .nav .nav-link {
    position: relative;
    height: 100%;
    min-height: 100%;
  }
  .sw-theme-default > .nav .nav-link::after {
    content: '';
    position: absolute;
    height: 2px;
    width: 0;
    left: 0;
    bottom: -1px;
    background: #999;
    transition: all 0.35s ease 0.15s;
  }
  .sw-theme-default > .nav .nav-link.inactive {
    color: #999;
    cursor: not-allowed;
  }
  .sw-theme-default > .nav .nav-link.active {
    color: #17a2b8 !important;
    cursor: pointer;
  }
  .sw-theme-default > .nav .nav-link.active::after {
    background: #17a2b8 !important;
    width: 100%;
  }
  .sw-theme-default > .nav .nav-link.done {
    color: #5cb85c !important;
    cursor: pointer;
  }
  .sw-theme-default > .nav .nav-link.done::after {
    background: #5cb85c;
    width: 100%;
  }
  .sw-theme-default > .nav .nav-link.disabled {
    color: #ddd !important;
    cursor: not-allowed;
  }
  .sw-theme-default > .nav .nav-link.disabled::after {
    background: #ddd;
    width: 100%;
  }
  .sw-theme-default > .nav .nav-link.danger {
    color: #d9534f !important;
    cursor: pointer;
  }
  .sw-theme-default > .nav .nav-link.danger::after {
    background: #d9534f;
    width: 100%;
  }
  // .sw-theme-arrows {
  //     /*border:1px solid #eee*/
  // }
  .sw-theme-arrows > .tab-content > .tab-pane {
    padding: 10px;
  }
  .sw-theme-arrows .toolbar > .btn {
    color: #fff;
    background-color: #17a2b8;
    border: 1px solid #17a2b8;
    padding: 0.375rem 0.75rem;
    border-radius: 0.25rem;
    font-weight: 400;
  }
  .sw-theme-arrows > .nav {
    overflow: hidden;
    /*border-bottom:1px solid #eee*/
  }
  .sw-theme-arrows > .nav .nav-link {
    position: relative;
    height: 100%;
    min-height: 100%;
    margin-right: 30px;
    margin-left: -30px;
    padding-left: 40px;
    padding-right: 0;
  }
  @media screen and (max-width: 640px) {
    .sw-theme-arrows > .nav .nav-link {
      overflow: hidden;
      margin-bottom: 1px;
      margin-right: unset;
    }

    .sw > .nav {
      flex-direction: column !important;
      flex: 1 auto;
    }

    .sw-theme-arrows > .nav .nav-link {
      overflow: hidden;
      margin-bottom: 1px;
      margin-right: unset;
    }
  }
  .sw-theme-arrows > .nav .nav-link::after {
    content: '';
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    top: 50%;
    left: 100%;
    margin-left: -1px;
    margin-top: -50px;
    border-top: 50px solid transparent;
    border-bottom: 50px solid transparent;
    border-left: 30px solid #f8f8f8;
    z-index: 2;
  }
  .sw-theme-arrows > .nav .nav-link::before {
    content: ' ';
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    top: 50%;
    left: 100%;
    margin-top: -50px;
    margin-left: 1px;
    border-top: 50px solid transparent;
    border-bottom: 50px solid transparent;
    border-left: 30px solid #ddd;
    z-index: 1;
  }
  .sw-theme-arrows > .nav .nav-link.inactive {
    color: #999;
    border-color: #f8f8f8;
    background: #f8f8f8;
    cursor: not-allowed;
  }
  .sw-theme-arrows > .nav .nav-link.active {
    color: #fff;
    border-color: #2c62d6;
    background: #2c62d6;
    cursor: pointer;
  }
  .sw-theme-arrows > .nav .nav-link.active::after {
    border-left-color: #2c62d6;
  }
  .sw-theme-arrows > .nav .nav-link.done {
    color: #fff;
    border-color: #5cb85c;
    background: #5cb85c;
    cursor: pointer;
  }
  .sw-theme-arrows > .nav .nav-link.done::after {
    border-left-color: #5cb85c;
  }
  .sw-theme-arrows > .nav .nav-link.disabled {
    color: #eee;
    border-color: #f9f9f9;
    background: #f9f9f9;
    cursor: not-allowed;
  }
  .sw-theme-arrows > .nav .nav-link.disabled::after {
    border-left-color: #f9f9f9;
  }
  .sw-theme-arrows > .nav .nav-link.danger {
    color: #fff;
    border-color: #d9534f;
    background: #d9534f;
    cursor: pointer;
  }
  .sw-theme-arrows > .nav .nav-link.danger::after {
    border-left-color: #d9534f;
  }
  .sw.sw-justified > .nav .nav-link,
  .sw.sw-justified > .nav > li {
    flex-basis: 0;
    flex-grow: 1;
    text-align: center;
  }
  /* End step nav */

