    font-weight: 300;
    color: #333;
    line-height: 0.8;
    border-radius: 50%;
    transition: ease 0.25s;
    background: #c1c1c1;
    border-color: #c1c1c1;
  }
}

// 01/08/2021
.parent {
  overflow: hidden;
}
.draggable {
  background: #fff;
  z-index: 10;
}
.signer-contain {
  overflow-y: unset !important;
  height: 100%;
  z-index: 2;
  height: calc(100vh - 254px);

  .draggable {
    position: fixed;
    z-index: 9999;
    transition: none !important;
    -webkit-transition: none !important;
    -moz-transition: none !important;
    -ms-transition: none !important;
    -o-transition: none !important;
  }
  .draggable.dropped {
    position: static;
    transform: none !important;
    -webkit-transform: none !important;
    -moz-transform: none !important;
    -ms-transform: none !important;
    -o-transform: none !important;
  }
}

.preview-contain {
  position: relative;
  z-index: 1;
  overflow: unset !important;
  padding: 0 !important;
}
.preview-pdf-overlay {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 0.5rem;
  overflow-y: auto;
}

.preview-signature-overlay {
  position: absolute;
  left: 0;
  top: 0;
  height: 0;
  z-index: 2;
  .signature_inpreview {
    overflow: unset;
    .signer_name {
      font-size: 14px;
    }
  }
  .draggable {
    width: auto;
    height: auto;
    z-index: 9999;
    min-width: 136px;
    height: auto;
    min-height: 80px;
  }
  .draggable {
    position: fixed;
  }
  .draggable.dropped {
    position: absolute;
  }
}

.pdf-page {
  canvas {
    margin: 0 auto;
  }
}
.doc_preview {
  display: flex;
  flex-flow: column;
}
.create-contract .preview_nav {
  flex: 0 0 27px;
  max-height: 27px;
}
.show-file-upload {
  max-width: calc(100% - 261px);
  margin: 0 auto;
}
.show-file-name b {
  max-width: 100%;
  overflow: hidden;
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
}
@media (max-width: 736px) {
  .show-file-upload {
    max-width: 100%;
    padding: 0 20px;
  }
}
@media (max-width: 575px) {
  .show-file-upload {
    max-width: 100%;
    padding: 0 20px;
  }
  .show-file-name b {
    max-width: 100%;
    overflow: hidden;
    display: block;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
@media (max-width: 575px) {
  .signer-contain {
    .simplebar-content {
      display: flex;
    }
  }
}
.signers::after {
  content: ' ';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50px;
  background: linear-gradient(to top, #fff 25%, rgba(255, 255, 255, 0.35));
}

.new_contract .pdf-page-container.create-page-contain {
  position: relative;
  .react-pdf__Page,
  .react-pdf__Page__canvas {
    pointer-events: none !important;
    * {
      pointer-events: none !important;
    }
  }
  // &.dragging {
  //   .signature_inpreview .signer_name{
  //     pointer-events: none;
  //   }
  // }
}

@media (max-width: 767px) {
  .signers {
    .simplebar-content {
      display: flex;
    }
  }
}

